import React from "react";
import Leaflet from "leaflet";
import { EsriProvider as provider } from "leaflet-geosearch";
import { Map, Marker, Popup, TileLayer } from "react-leaflet";
import "leaflet/dist/leaflet.css";
import CovidCard from "../../Component/CovidCard/CovidCard";

delete Leaflet.Icon.Default.prototype._getIconUrl;

Leaflet.Icon.Default.mergeOptions({
  iconRetinaUrl: require("leaflet/dist/images/marker-icon-2x.png"),
  iconUrl: require("leaflet/dist/images/marker-icon.png"),
  shadowUrl: require("leaflet/dist/images/marker-shadow.png")
});

export default class extends React.Component {
  provider = new provider();
  state = {
    results: []
  };

  componentDidMount() {
    this.props.stats.map(item =>
      this.provider.search({ query: item.country_name }).then(results => {
        if (results[0]) {
          const coord = { ...item, coords: [results[0].y, results[0].x] };
          this.setState(state => {
            return {
              results: [...state.results, coord]
            };
          });
        }
      })
    );
  }

  render() {
    return (
      <Map center={[0, 0]} zoom={2} style={{ height: "800px" }}>
        <TileLayer
          attribution='&amp;copy <a href="http://osm.org/copyright">OpenStreetMap</a> contributors'
          url="https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png"
        />
        {this.state.results.map((item, key) => (
          <Marker position={item.coords}>
            <Popup>
              <CovidCard {...item} />
            </Popup>
          </Marker>
        ))}
      </Map>
    );
  }
}
